.downloader{padding:20px;margin:20px;color:#000;font-family:"Poppins",sans-serif;text-align:left;background-color:#fff;border:5px solid #fff;box-sizing:unset;-moz-box-sizing:unset;-webkit-box-sizing:unset;letter-spacing:0;display:flex;flex-direction:row;align-items:center;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}.downloader .image-preview{width:588px;height:588px;overflow:hidden;position:relative;margin-right:64px;border-radius:12px;background-color:rgba(0,0,0,0.04)}.downloader .image-preview canvas{position:absolute}.downloader .image-preview .blur{-webkit-filter:blur(10px);filter:blur(10px)}.downloader .image-preview .status{width:100%;height:100%;background-color:rgba(0,0,0,0.5);position:absolute;top:0;left:0;color:white;font-size:2em;display:flex;align-items:center;justify-content:center}.downloader .download-panel{white-space:nowrap}.downloader .download-panel .title-container{margin-bottom:40px}.downloader .download-panel p{font-family:"Poppins",sans-serif;font-size:16px;color:rgba(0,0,0,0.4);letter-spacing:1.33px;height:24px;margin:0;padding:0}.downloader .download-panel .art-title{font-family:"Poppins",sans-serif;font-size:40px;color:#000000;letter-spacing:0;height:30px;display:flex;align-items:top}

.centered-item{display:flex;flex:1 1 auto;align-items:center;flex-direction:column;justify-content:center}

.disabled{pointer-events:none;opacity:0.75;cursor:unset;-webkit-filter:saturate(0);filter:saturate(0)}*:focus{outline:none}.upload-group{margin-right:150px}.upload-group .centered-item{flex-direction:row;justify-content:space-evenly;margin-top:20px}.upload-group .centered-item p{cursor:pointer}.upload-group .centered-item p:hover{text-decoration:underline}.upload-group .upload-panel{width:504px;display:flex;justify-content:space-between}.upload-group .upload-panel .upload-result .img-holder{width:320px;height:320px;border:1px solid rgba(217,223,233,0.4);background-size:contain;background-repeat:no-repeat;background-position:center;background-color:rgba(0,0,0,0.04);display:flex;align-items:center}.upload-group .upload-panel .upload-result .img-holder .loading-value{position:relative;left:50%;transform:translateX(-50%);width:110px;color:#000;font-size:1em;text-align:right;text-shadow:0px 0px 5px #ffffff, 0px 0px 5px #ffffff, 0px 0px 5px #ffffff;text-shadow:0px 0px 5px #fff,0px 0px 5px #fff,0px 0px 5px #fff}.upload-group .upload-panel .upload-result p{font-size:1em;text-align:center}.upload-group .upload-panel .upload-source .upload{height:320px;position:relative;justify-content:space-between;border-radius:6px;overflow:hidden}.upload-group .upload-panel .upload-source .upload .progress-bar{background-color:rgba(0,0,0,0.04);width:100%;height:100%;transform-origin:50% 100%;transition:0.5s;position:absolute;transform:scaleY(0)}.upload-group .upload-panel .upload-source .upload .file-dropbox{background:rgba(0,0,0,0.04);width:156px;height:445px;z-index:1;display:flex;flex-direction:column;justify-content:center;align-items:center;cursor:pointer}.upload-group .upload-panel .upload-source .upload .file-dropbox p{font-size:0.5em;text-align:center}.upload-group .upload-panel .upload-source .upload .file-dropbox img{opacity:0.5}.upload-group .upload-panel .upload-source .upload .file-dropbox:hover img{opacity:1}.upload-group .upload-panel .upload-source .upload .file-dropbox:hover p{text-decoration:underline}.upload-group .upload-panel .upload-source p{font-size:16px;color:#000;cursor:pointer}.upload-group .upload-panel .upload-source p:hover{text-decoration:underline}

.mocker{display:flex;flex-direction:column;margin-left:130px}.uploader{padding:20px;margin:20px;color:#000;font-family:"Poppins",sans-serif;text-align:left;width:510px;height:600px;background-color:#fff;border:5px solid #fff;box-sizing:unset;-moz-box-sizing:unset;-webkit-box-sizing:unset;letter-spacing:0;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}.uploader .header{font-size:24px;color:#000000;letter-spacing:0}.uploader .enter-title{width:225px;margin-bottom:20px;border-radius:4px;font-size:14px;color:#000000;line-height:20px}.uploader .enter-title input{font-family:"Poppins",sans-serif;width:225px;padding:10px;border:2px solid #f2f2f2;border-radius:4px}.uploader .row{display:flex}

